<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import { formatDate } from "@/util";

export default {
  name: "Reccognition",
  data() {
    return {
      interval: 1000,
      scale:1,
      duration: 105450,
      periods: [
        {
          uid: "user12345",
          username: "张三",
          startTime: 4568,
          endTime: 18755,
          words: "这真的是传说中的饕餮吗?"
        },
        {
          uid: "user12346",
          username: "李四",
          startTime: 20598,
          endTime: 28111,
          words: "快看，他要发起攻击了。"
        },
        {
          uid: "user12345",
          username: "张三",
          startTime: 30111,
          endTime: 34555,
          words: "我们快走"
        },
        {
          uid: "user12347",
          username: "王五",
          startTime: 40688,
          endTime: 75998,
          words:
            "伙计们，快走，我来顶住。你们去找菩提老祖求助，他正在西湖边上。"
        }
      ],
      colorObj:{},
    };
  },
  computed: {
    compArr() {
      let count = Math.ceil(this.duration / this.interval);
      let arr = [];
      for (let i = 1; i <= count; i++) {
        arr.push(this.interval * i);
      }
      return arr;
    },
    compPeriods() {
      let duration = Math.ceil(this.duration / this.interval) * this.interval;
      let periods = this.periods.map(item => {
        item.left = (item.startTime / duration) * 100;
        item.right = 100 - (item.endTime / duration) * 100;
        if (!this.colorObj[item.uid]) {
          let max = 200;
          this.colorObj[item.uid] = `rgb(${Math.floor(
            Math.random() * max
          )},${Math.floor(Math.random() * max)},${Math.floor(
            Math.random() * max
          )})`;
        }
        item.color = this.colorObj[item.uid];
        return item;
      });
      return periods;
    }
  },
  mounted() {},
  created() {
    this.intervalInit = this.interval
    this.scaleInit = this.scale
  },
  methods: {
    refresh(){
      this.interval = this.intervalInit
      this.scale = this.scaleInit
    },
    reduce(){
      if(this.scale >= 4) return
      this.scale *= 2
      this.interval = this.intervalInit * this.scale
    },
    enlarge(){
      if(this.scale <= 0.25) return
      this.scale /= 2
      this.interval = this.intervalInit* this.scale
    },
  }
};
</script>

